import { getComments, addComment } from "@/api/article.js"
export default {
  name: "ArticleComments",
  props: {
    article: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      comments: [],
      comment: {
        body: "",
      },
      disabled: false,
    }
  },
  mounted() {
    this.getComments()
  },
  methods: {
    async getComments() {
      const { data } = await getComments(this.article.slug)
      this.comments = data.comments
    },
    async addComment() {
      this.disabled = true
      let { data } = await addComment(
        {
          comment: this.comment,
        },
        this.article.slug
      )
      console.log(data)
      this.disabled = true
      this.comments.unshift(data.comment)
    },
  },
}
